.btn {
  position: relative;
  display: inline-block;
  font-family: inherit;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: .5px;
  padding: 1rem 2rem;
  color: #2c4267;
  background: #fff;
  border: none;
  border-radius: 4px;
  outline: none;
  box-shadow: 10px 5px 25px rgba(44, 66, 103, 0.1111);
  cursor: pointer;
  transition: all .25s ease; }
  .btn:hover, .btn:focus {
    box-shadow: 5px 5px 10px rgba(44, 66, 103, 0.2); }
  .btn:active {
    transform: translateY(3px);
    box-shadow: 2px 2px 8px rgba(44, 66, 103, 0.3); }

.btn-primary {
  color: #fff;
  background: #06d79c; }

